___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Titillium Web", sans-serif;
  font-weight: 600
}

button {
  background-color: transparent;
}

ul,
li {
  list-style-type: none;
}

body {
  margin: 0;
  font-optical-sizing: auto;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2b2b2b;
}

.spinner {
  position: absolute;
  background-color: rgba(234, 234, 234, 0.4);
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* scrollbar */

::-webkit-scrollbar {
  width: 0.5rem;
}

/* Track (background) of the scrollbar */
::-webkit-scrollbar-track {
  background: #fff;
  border-top-right-radius: 2rem;
  border-bottom-right-radius: 2rem;
}

/* Thumb (draggable part) of the scrollbar */
::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
  border-radius: 10px;
  /* Add padding around the thumb */
}

/* Hover effect on the thumb */
::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

::-webkit-scrollbar-corner {
  background-color: #f1f1f1;
}`, "",{"version":3,"sources":["webpack://./src/index.css"],"names":[],"mappings":"AAAA;EACE,UAAU;EACV,SAAS;EACT,sBAAsB;EACtB,wCAAwC;EACxC;AACF;;AAEA;EACE,6BAA6B;AAC/B;;AAEA;;EAEE,qBAAqB;AACvB;;AAEA;EACE,SAAS;EACT,yBAAyB;EACzB,kBAAkB;EAClB,mCAAmC;EACnC,kCAAkC;EAClC,cAAc;AAChB;;AAEA;EACE,kBAAkB;EAClB,0CAA0C;EAC1C,YAAY;EACZ,WAAW;EACX,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA,cAAc;;AAEd;EACE,aAAa;AACf;;AAEA,wCAAwC;AACxC;EACE,gBAAgB;EAChB,6BAA6B;EAC7B,gCAAgC;AAClC;;AAEA,4CAA4C;AAC5C;EACE,sCAAsC;EACtC,mBAAmB;EACnB,iCAAiC;AACnC;;AAEA,8BAA8B;AAC9B;EACE,sBAAsB;AACxB;;AAEA;EACE,yBAAyB;AAC3B","sourcesContent":["* {\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n  font-family: \"Titillium Web\", sans-serif;\n  font-weight: 600\n}\n\nbutton {\n  background-color: transparent;\n}\n\nul,\nli {\n  list-style-type: none;\n}\n\nbody {\n  margin: 0;\n  font-optical-sizing: auto;\n  font-style: normal;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  color: #2b2b2b;\n}\n\n.spinner {\n  position: absolute;\n  background-color: rgba(234, 234, 234, 0.4);\n  height: 100%;\n  width: 100%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n/* scrollbar */\n\n::-webkit-scrollbar {\n  width: 0.5rem;\n}\n\n/* Track (background) of the scrollbar */\n::-webkit-scrollbar-track {\n  background: #fff;\n  border-top-right-radius: 2rem;\n  border-bottom-right-radius: 2rem;\n}\n\n/* Thumb (draggable part) of the scrollbar */\n::-webkit-scrollbar-thumb {\n  background-color: var(--primary-color);\n  border-radius: 10px;\n  /* Add padding around the thumb */\n}\n\n/* Hover effect on the thumb */\n::-webkit-scrollbar-thumb:hover {\n  background-color: #555;\n}\n\n::-webkit-scrollbar-corner {\n  background-color: #f1f1f1;\n}"],"sourceRoot":""}]);
